.test{
  position: relative;
  display: block;
  padding:15px 5px; 
  width:350px;
  height:400px;
  margin:20px;
  border-radius: 5px;
  background-color:rgb(19, 102, 156);
  transition:0.6s ease;
  clip-path: polygon(51.43% 8.75%, 100% 0px, 92% 85.25%, 50% 100%, 26px 84%, 0px 0px);
  
}

.test::after{
  position: absolute;
  height: 10px;
  width: 100px;
  top:140px;
  left:-30px;
  background: #114368;
  border-bottom-right-radius: 50px;
  border-right: 1px white;
  transform: rotate(5deg);
  content: '';
}
.test::before{
  position: absolute;
  height: 10px;
  width: 100px;
  top:140px;
  right:-26px;
  background: #114368;
  border-bottom-left-radius: 10px;
  border-right: 1px white;
  transform: rotate(-5deg);
  content: '';
}



.test:hover{
  box-shadow:inset -5px -5px 50px rgb(25, 56, 112),
             inset -5px -5px 50px rgb(60, 110, 202,0.5),
             inset  5px 5px 100px rgb(60, 110, 202,0.1),
             inset 5px -5px 100px rgb(60, 110, 202,0.15);
  transform: scale(1.05);
  
  transition:0.4s ease;
}

.test p{
 
  color:rgb(17, 28, 31);
  text-indent: 10%;
  margin:10% 10%;
  text-align: justify;
  font-size: 1.2rem;
  font-family: 'Noto Sans KR', ;
}







.moreButton{
  cursor: pointer;
  position: absolute;
  color:rgb(255, 255, 255);
  background:rgb(29, 119, 179);
  font-size:1rem;
  font-family: 'Noto Sans KR' ;
  padding: 12px;
  border:none;
  top:45%;
  left:45%;
  transition-duration: 0.4s;
  animation: moreButton 0.5s infinite;
  z-index:10;
 
}



.moreButton span{
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  height: 100%;
  display: block;
  box-sizing: border-box;
  
}

.moreButton  span:nth-child(1)
{
  transform:rotate(0deg);
  left:-50%;
}

.moreButton span:nth-child(2)
{
  height: 100%;
  width:26%;
  top:-50%;
  left:74%;
  transform:rotate(90deg);
}

.moreButton  span:nth-child(3)
{
  transform:rotate(180deg);
  
}

.moreButton  span:nth-child(4)
{
  transform:rotate(270deg);
  width:26%;
  left:0%;
  top:51%;
}

.moreButton span:before
{
  content: '';
  position: absolute;
  width:100%;
  height: 2px;
  background: #133646;
  animation: borderBox 3s linear infinite;
}

@keyframes borderBox {
  0% {
  transform:scaleX(0);
  transform-origin: left;
  }
  50%
  {
    transform:scaleX(1);
  transform-origin: left;
  }
  50.1%
  {
    transform:scaleX(1);
  transform-origin: right;
    
  }
  
  100%
  {
    transform:scaleX(0);
  transform-origin: right;
    
  }
  
  
} 

.moreButton:hover span:before{
  background:rgb(22, 99, 151);
  -webkit-animation-play-state:paused;
  -moz-animation-play-state:paused;
  -o-animation-play-state:paused;
  animation-play-state:paused;
}
.moreButton:hover{
  background:rgb(22, 99, 151); 
  color:black;
}


