.searchButton{
  position: relative;
  background-color: rgb(58, 156, 221);
  color:rgb(15, 17, 24);
  font-family: 'Noto Sans KR' ;
  font-size: 1.2rem;
  display:inline;
  margin-left: 1%;
  border-radius:5px;
  border:1px solid rgb(8, 27, 78);
  padding: 5px 30px;
  padding-top: 8px;
  transition: 0.8s;
  
}

.searchButton:hover{
  background-color: rgb(43, 115, 197);
  color:rgb(38, 50, 87);
  
  cursor: pointer;
}


.searchButton .tooltiptext{
  visibility: hidden;
  width: 120px;
  background-color: rgb(47, 138, 212);
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  top:50px;
  left:-10px;
  position: absolute;
  z-index: 1;
  opacity: 0;
  transition: visibility 0s, opacity 0.5s linear;
}

.searchButton:hover .tooltiptext{
  visibility: visible;
  opacity: 1;
}

.boxSearch{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(550px, 1fr));
  grid-column-gap:50px;
  grid-row-gap:30px;
  width:70%;
  margin:0 auto;
  margin-top:3%;
  transition: 0.8s;
 
}

.searchContent{
  background-color: rgb(77, 146, 185);
  padding:50px 25px;
  border-radius: 20px;
  border-bottom: 5px solid rgb(24, 37, 54);
  max-width: 750px;
  margin:0 auto;
  margin-top:20px;
  box-shadow: 2px 2px 10px 2px black;
  
 
}

.searchContent p{
  font-family: 'Spartan', sans-serif;
  font-size: 2rem;
  color:rgb(10, 29, 41);
}



#typing-loader{
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  animation: typing 1s linear infinite alternate;
  position: relative;
  left: -3rem;
  margin: 0 auto;
}

@keyframes typing{
  0%{
      background-color: rgba(0,0,0, 1);
      box-shadow: 3.2rem 0 0 0 rgba(0,0,0,0.2),
      6.4rem 0 0 0 rgba(0,0,0,0.2);
  }
  25%{
      background-color: rgba(0,0,0, 0.4);
      box-shadow: 3.2rem 0 0 0 rgba(0,0,0,2),
      6.4rem 0 0 0 rgba(0,0,0,0.2);
  }
  75%{ background-color: rgba(0,0,0, 0.4);
      box-shadow: 3.2rem 0 0 0 rgba(0,0,0,0.2),
      6.4rem 0 0 0 rgba(0,0,0,1);
  }
}






